#Help_main_top img {
    width: 100%;
    margin-top: 8rem;
    height: 750px;
}

#Help_main_top {
    margin-top: 2rem;
}

.help_main_top_img_text {
    position: absolute;
    top: 50%;
    left: 31.5%;
    text-align: center;
    color: #fff;
}

.help_main_top_img_text h1 {
    font-weight: bold;
    margin-bottom: 1rem;
    font-size: 30px;
}

.help_main_top_img_text p {
    font-size: 13px;
}

.help_main_top_img_text button {
    margin-top: 1rem;
    padding: 14px 80px 14px 80px;
    font-size: 11px;
    background-color: transparent;
    border: 1px solid white;
    border: 1px solild white;
    color: white;
}

.help_main_top_left {
    position: absolute;
    top: 100%;
    text-align: center;
    color: #fff;
    left: 10%;
}

.help_main_top_left h4 {
    font-size: 20px;
}

.help_main_top_left p {
    font-size: 13px;
}

.help_main_top_right {
    position: absolute;
    top: 100%;
    text-align: center;
    color: #fff;
    right: 10%;
}

.help_main_icon {
    margin-top: -20px;
    font-size: 50px;

}

.help_text_main {
    margin-top: 5rem;
    text-align: center;
}

.help_text_main h1 {
    font-size: 25px;
    font-weight: bold;
}

.help_text_main p {
    font-size: 18px;
    margin-top: 20px;
    margin-bottom: 20px;
}

.help_text_main h6 {
    font-size: 10px;
    font-weight: bold;
}

#input {
    margin-top: 10rem;
    text-align: center;
}

.input_top {
    margin-bottom: 2rem;
}

.input_top span {
    font-size: 13px;
}

.input_main {
    margin-bottom: 3rem;
    margin-left: 37rem;
    width: 30%;
}

.input_main_group button {
    padding: 14px 232px 14px 232px;
    background-color: #212931;
    color: #fff;
    font-size: 12px;
    border-radius: 5px;
}

.input_main_group button:hover {
    background-color: black;
    transition: .9;
}

@media(max-width: 576px) {
    .input_main {
        margin-bottom: 3rem;
        margin-left: 1px;
        width: 100%;
    }

    .input_main_group button {
        padding: 14px 180px 14px 180px;
        background-color: #212931;
        color: #fff;
        font-size: 12px;
        border-radius: 5px;
    }

    .help_main_top_img_text {
        position: absolute;
        top: 20%;
        left: 1px;
        text-align: center;
        color: #fff;
        width: 100%;
    }

    .help_main_top_right {
        display: none;
    }

    .help_main_top_left {
        display: none;
    }

    #Help_main_top img {
        width: 100%;
        margin-top: 0rem;
        height: 379px;
    }
}