.left img {
    width: 100%;
    height: 700px;
    object-fit: cover;
    margin-top: 7rem;
}

.rows {
    display: flex;
}

.aboutbutton {
    position: absolute;
    top: 29rem;
    left: 21rem;
    background-color: transparent;
    border: 1px solid #fff;
    padding: 13px 36px 14px 36px;
    color: #fff;
    font-size: 10px;
}

.aboutbutton:hover {
    background-color: #fff;
    color: black;
    transition: 0.6s;
}

.abouttext {
    margin-top: 17rem;
    margin-left: 3rem;
}

.abouttext h1 {
    color: #212121;
    font-size: 27px;
    font-weight: 600;

}

.abouttext p {
    color: #212121;
    line-height: 20px;
    font-size: 14px;
    margin-bottom: 40px;
    font-weight: 400;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Ubuntu, "Helvetica Neue", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
}

.abouttext h6 {
    color: #212121;
    font-size: 14px;
    font-weight: 400;
}

#about3 {
    margin-top: 10rem;
}

#about3 h1 {
    margin-bottom: 2rem;
    color: #212121;
    font-size: 35px;
    line-height: 35px;
    font-weight: bold;
}

#about3 h6 {
    font-weight: bold;
    margin-top: 2rem;
}

.alls {
    margin-left: 35%;
}

.all img {
    width: 100%;
    height: 800px;
    object-fit: cover;
}

.all {
    position: relative;
    z-index: -1;
}

.aabouttext {
    position: absolute;
    top: 25rem;
    left: 25%;
    text-align: center;
}

.aabouttext h2 {
    color: #fff;

}

.aabouttext p {
    color: #fff;
    font-size: 14px;
}

.lefts img {
    width: 100%;
    height: 700px;
    object-fit: cover;
}

.right {
    display: flex;
}

.right-right {
    margin-top: 20rem;
}

.text {
    text-align: center;
}

.text h1 {
    font-size: 25px;
    font-weight: 600;
}

.text p {
    margin-top: 1rem;
    font-size: 13px;
}

.text8 {
    margin-top: 5rem;
    margin-left: 12rem;
}

.text8 h2 {
    font-size: 25px;
}

.rows8 {
    overflow: hidden;
    margin-top: 7rem;
    display: flex;
    padding: 0 2rem;
}

.rows8 h5 {
    margin-top: 1rem;
    font-size: 17px;
    font-weight: 700;
}

.rows8 p {
    font-size: 12px;
    font-weight: 600;
}

.about8 {
    margin-top: 6rem;
    text-align: center;
    margin-bottom: 10rem;
}

.about8 h3 {
    font-size: 23px;
    font-weight: bold;
}

.about8 p {
    font-size: 13px;
}

.about8 input {
    padding: 10px 10px 10px 10px;
}

.about8 button {
    padding: 10px 14px 10px 18px;
    background-color: black;
    color: #fff;
    border-radius: 5px;
}

.about8 input {
    border: 3px solid ghostwhite;
}

@media (max-width:576px) {
    .rows {
        flex-direction: column;
    }

    .rows8 {
        flex-direction: column;
    }

    .rows8 img {
        width: 100%;
    }

    .aboutbutton {
        position: absolute;
        left: 100px;
    }

    .alls {
        margin-left: 10px;
    }

    .text8 {
        margin-left: 10px;
    }

}

@media(max-width: 576px) {
    #about1 .right {
        margin-top: -14rem;

    }

    .abouttext {
        margin-left: 1rem;
    }

    #about2 img {
        margin-top: -72rem;
        width: 100%;
        height: 400px;
    }

    .abouttext p {
        margin-top: 5rem;
    }

    .alls {
        margin-left: 10px;
        margin-top: -10rem;
    }

    .aabouttext {
        position: absolute;
        top: 20rem;
        text-align: center;
        width: 100%;
        margin-left: -96px;
    }
    .right.abouttext{
        margin-top: 3rem;
    }
    .abouttext{
        margin-top: 3rem;
    }
}