.menu{
    color: black;
    font-size: 25px;
    z-index: -2;
    cursor: pointer;
}
.person{
    color: black;
    font-size: 25px;
    margin-right: 15px;
    z-index: 1;
}
.righticon{
    z-index: -1;
    font-size: 20px;
    color: black;
}
.row{
    padding: 0 2rem;
    align-items: center;
    width: 100%;
}
.search{
    color: black;
    font-size: 25px;
    margin-left: 10px;
    position: absolute;
}
@media (max-width: 576px){
    .row{
        padding: 0 0rem;
    }
}